import vueTreeTable from './tree-table.vue'

const TreeTable = {
  install: function (Vue) {
    Vue.component('TreeTable', vueTreeTable)
  }
};

// 
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(TreeTable)
}

export default TreeTable


